@use './Resources/SASS/colors.scss';

.App {
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;

  &.dark {
    background: colors.$bg-dark;
    color: colors.$fg-dark;
  }

  &.light {
    background: colors.$bg-light;
    color: colors.$fg-light;
  }
}